<template>
    <v-container id="aluguelPage">
        <v-row>
            <v-col>
                <v-sheet rounded="xl">
                    <v-data-table
                        class="rounded-xl elevation-7"
                        :headers="headers"
                        :items="rents"
                        :items-per-page="5"
                        sort-by="id"
                    >
                        <template v-slot:item.acoes="{ item }">
                            <v-tooltip top color="#0061A3">
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                        color="primary"
                                        v-bind="attrs"
                                        v-on="on"
                                        text
                                        small
                                        rounded
                                        @click="showEdit(item)"
                                    >
                                        <v-icon dark> mdi-pencil </v-icon>
                                    </v-btn>
                                </template>
                                <span>Editar</span>
                            </v-tooltip>
                            <v-tooltip top color="red">
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                        color="error"
                                        v-bind="attrs"
                                        v-on="on"
                                        text
                                        small
                                        rounded
                                        @click="remover(item)"
                                    >
                                        <v-icon dark> mdi-delete </v-icon>
                                    </v-btn>
                                </template>
                                <span>Remover</span>
                            </v-tooltip>
                        </template>
                    </v-data-table>
                </v-sheet>
            </v-col>
        </v-row>

        <v-row justify="center">
            <v-dialog v-model="dialog" persistent max-width="600px">
                <template v-slot:activator="{ on, attrs }">
                    <v-btn color="#FF0038" dark v-bind="attrs" v-on="on">
                        <font-awesome-icon icon="fa-solid fa-plus" />
                        <v-spacer></v-spacer>
                        Alugar
                    </v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="text-h5">Aluguel Info </span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="12">
                                    <v-text-field label="livro" append-icon="mdi-book" required></v-text-field>
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field label="leitor" append-icon="mdi-account" required></v-text-field>
                                </v-col>
                                <v-col cols="12">
                                    <v-dialog
                                        ref="dialog"
                                        v-model="modal"
                                        :return-value.sync="date"
                                        persistent
                                        width="290px"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                            <v-text-field
                                                v-model="date"
                                                label="Data de aluguel"
                                                append-icon="mdi-calendar"
                                                readonly
                                                v-bind="attrs"
                                                v-on="on"
                                            ></v-text-field>
                                        </template>
                                        <v-date-picker v-model="date" scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="modal = false"> Cancel </v-btn>
                                            <v-btn text color="primary" @click="$refs.dialog.save(date)"> OK </v-btn>
                                        </v-date-picker>
                                    </v-dialog>
                                </v-col>
                                <v-col cols="12">
                                    <v-dialog
                                        ref="dialog"
                                        v-model="modal"
                                        :return-value.sync="date"
                                        persistent
                                        width="290px"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                            <v-text-field
                                                v-model="date"
                                                label="Data de previsão"
                                                append-icon="mdi-calendar"
                                                readonly
                                                v-bind="attrs"
                                                v-on="on"
                                            ></v-text-field>
                                        </template>
                                        <v-date-picker v-model="date" scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="modal = false"> Cancel </v-btn>
                                            <v-btn text color="primary" @click="$refs.dialog.save(date)"> OK </v-btn>
                                        </v-date-picker>
                                    </v-dialog>
                                </v-col>
                            </v-row>
                        </v-container>
                        <small>*Todos os campos são obrigatórios</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="dialog = false"> Cancelar </v-btn>
                        <v-btn color="blue darken-1" text @click="dialog = false"> Salvar </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-row>
    </v-container>
</template>

<script>
import Rent from '../service/rent';

export default {
    data: () => ({
        date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
        modal: false,

        dialog: false,

        headers: [
            { text: 'ID', value: 'id', aling: 'center' },
            { text: 'LIVRO', value: 'book.name', aling: 'center' },
            { text: 'LEITOR', value: 'user.name', aling: 'center' },
            { text: 'DATA DE ALUGUEL', value: 'rentDate', aling: 'center' },
            { text: 'DATA PREVISTA', value: 'rentPredict', aling: 'center' },
            { text: 'DATA ENTREGUE', value: 'devolutionDate', aling: 'center' },
            { text: 'ESTATUS', value: 'status', aling: 'center' },
            { text: '', value: 'acoes', sortable: false }
        ],

        rents: []
    }),

    mounted() {
        Rent.getAllRents().then((response) => {
            console.log(response);
            this.rents = response.data.content;
        });
    }
};
</script>

<style>
#aluguelPage {
    background-color: #1f1f1f;
}
</style>
